import Vue from "vue";
import Vuex from "vuex";
import Cookies from "js-cookie";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    scroll: "10",
    locale: Cookies.get("locale") || "ar",
  },
  mutations: {
    getScrollValue(state, paylod) {
      state.scroll = paylod;
    },
    setLocale(state, paylod) {
      state.locale = paylod;
    },
  },
  actions: {},
  modules: {},
});
